// Global and reusable colors go in this file.
// A convention I use is to put a _ in front of a color so it's clear
// that this is a custom color when using it in a components styles.js file

module.exports = {
  brand: '#6766a2',
  _border: '#ececec',
  _blue: '#3A4769',
  _grey: '#757575'
};



